* {
  font-family: "IBM Plex Mono", monospace;
  -webkit-font-feature-settings: "salt" on;
          font-feature-settings: "salt" on;
  font-size: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

ul {
  list-style: none;
}

body {
  color: #161616;
}

h1,
h1 * {
  font-size: 6.84rem;
  font-weight: 700;
}

h2,
h2 * {
  font-weight: 450;
  font-size: 4rem;
}

h3,
h3 * {
  font-size: 2.618rem;
}

h4,
h4 * {
  font-size: 1.618rem;
}

.flex-component {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.align-center {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.height-100vh {
  height: 100vh;
}

.mt__bg-dark {
  background-color: #393939;
}

.mt__t-color--blue {
  color: #0f62fe;
}

.mt__t-color--white {
  color: #ffffff;
}

.mt__t-color--grey {
  color: #525252;
}

.mt__t-font--plex-serif,
.mt__t-font--plex-serif * {
  font-family: "IBM Plex Serif", serif;
}

.margin-left {
  margin-left: 10vw;
}

@-webkit-keyframes cursor {
  0% {
    background-color: white;
  }
  50% {
    background-color: #e0e0e0;
  }
  100% {
    background-color: white;
  }
}

@keyframes cursor {
  0% {
    background-color: white;
  }
  50% {
    background-color: #e0e0e0;
  }
  100% {
    background-color: white;
  }
}

.mt__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 90vh;
}

.jumbotron__wrapper__title {
  max-width: 70vw;
}

.btn-wrapper {
  height: 10vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.btn-wrapper--none a {
  display: none;
}

.btn-wrapper a {
  padding: 10px;
  border-radius: 50%;
}

.btn-wrapper a .fa-chevron-down {
  font-size: 30px;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  -webkit-animation: bounce 2s infinite;
          animation: bounce 2s infinite;
  color: #e0e0e0;
}

.btn-wrapper a:hover .fa-chevron-down {
  font-size: 38px;
  color: #0f62fe;
}

@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
}

.mt__tag {
  background: inherit;
  border-radius: 10px;
  font-size: 1.618rem;
  padding: 0 1em;
  -webkit-box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
  -webkit-transition: all 0.35s ease-out;
  transition: all 0.35s ease-out;
  cursor: pointer;
  text-decoration: none;
}

.mt__tag--blue {
  color: #0f62fe;
  border: 1px solid #0f62fe;
}

.mt__tag--blue:hover {
  background: #0f62fe;
  color: #ffffff;
}

.mt__tag--pink {
  color: #ff006e;
  border: 1px solid #ff006e;
}

.mt__tag--pink:hover {
  background: #ff006e;
  color: #ffffff;
}

.mt__tag--orange {
  color: #fb5607;
  border: 1px solid #fb5607;
}

.mt__tag--orange:hover {
  background: #fb5607;
  color: #ffffff;
}

.mt__tag--purple {
  color: #8338ec;
  border: 1px solid #8338ec;
}

.mt__tag--purple:hover {
  background: #8338ec;
  color: #ffffff;
}

.mt__tag--yellow {
  color: #ffbe0b;
  border: 1px solid #ffbe0b;
}

.mt__tag--yellow:hover {
  background: #ffbe0b;
  color: #ffffff;
}

@media only screen and (max-device-width: 360px) {
  .mt__tag {
    font-size: 12px;
  }
}

.jumbotron__title {
  line-height: 1.2em;
}

.jumbotron__title__code {
  color: #e0e0e0;
  font-weight: 400;
}

.jumbotron__title__cursor {
  display: inline-block;
  width: 3px;
  height: 1.2em;
  background-color: #e0e0e0;
  -webkit-animation: cursor 0.9s infinite;
          animation: cursor 0.9s infinite;
}

.side-bar {
  position: fixed;
  right: 20px;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.side-bar__item {
  mix-blend-mode: color-burn !important;
}

.side-bar a {
  aspect-ratio: 1;
  display: block;
  padding: 6px;
  border-radius: 8px;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.side-bar a:hover {
  -webkit-transform: scale(1.09);
          transform: scale(1.09);
}

.my-stack-section .stack-wrapper h3 {
  margin: 4vh 0 2vh 0;
  color: #0f62fe;
}

.my-stack-section .stack-wrapper a {
  display: block;
  height: 48px;
  cursor: pointer;
}

.projects__image {
  -webkit-box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
          box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  width: 100%;
}

/* Portrait and Landscape */
@media only screen and (max-device-width: 1024px) {
  h1,
  h1 * {
    font-size: 5rem;
    font-weight: 700;
  }
}

@media (max-device-width: 650px) {
  * {
    font-size: 10px;
  }
  h1,
  h1 * {
    font-size: 3rem !important;
    font-weight: 700;
  }
  h2,
  h2 * {
    font-size: 2.5rem;
  }
  h3,
  h3 * {
    font-size: 2rem;
  }
  .side-bar a {
    height: 62px;
    display: block;
    padding: 6px;
    border-radius: 8px;
    -webkit-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
  }
  .side-bar a:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  .stack-img {
    width: 36px;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 812px) and (orientation: landscape) {
  .side-bar {
    display: none;
  }
}
